import React, {useState,useEffect} from 'react';
import '../../styles/Join.scss';
import {Link, useNavigate} from "react-router-dom";
function Join() {
    // 네이게이션 함수 사용 변수
    const navigate = useNavigate();
    // 전체 선택 여부 상태 변수
    const [isCheckedAll, setIsCheckedAll] = useState(false);
    // 각 체크박스 선택 여부 상태 변수
    const [checkItems, setCheckItems] = useState([]);
    // 에러 메시지 변수
    const [errorMessage ,setErrorMessage] = useState({
        agree_use: null,
        agree_personal : null,
        agree_age : null,
    }); // 필수 체크박스 미입력 메시지

    // 전체 체크박스 선택/해제 처리 함수
    const handleAllCheck = (checked) => {
        setIsCheckedAll(checked);
        if (checked) {
            // 모두 선택할 경우
            setCheckItems(['agree_use', 'agree_personal', 'agree_age']);
            // 유효성 검사 문구 제거
            setErrorMessage({
                agree_use: null,
                agree_personal : null,
                agree_age : null,
            });

        } else {
            // 모두 해제할 경우
            setCheckItems([]);
        }
    };

    // 개별 체크박스 선택 처리 함수
    const handleItemCheck = (itemId) => {
        if (checkItems.includes(itemId)) {
            // 이미 선택된 항목일 경우 해제
            setCheckItems(checkItems.filter((item) => item !== itemId));

        } else {
            // 선택되지 않은 항목일 경우 선택
            setCheckItems([...checkItems, itemId]);
            setErrorMessage((prevError)=>({
                ...prevError,
                [itemId]: null
            }));
        }

    };

    // 개별체크박스 상태 변화 시 전체 체크 박스 on/off
    useEffect(()=>{
        if(checkItems.length>=3){
            setIsCheckedAll(true);
            console.log(isCheckedAll);
        }else{
            setIsCheckedAll(false);
        }
    },[checkItems])

    // 유효성 검사 함수
    const nextPage = (e)=>{
        e.preventDefault();
        if(checkItems.length>=3){
            navigate("/joinform");
        }else{
            Object.keys(errorMessage).forEach((key) => {
                if(!checkItems.includes(key))
                setErrorMessage((preError)=>({
                    ...preError,
                    [key] : '필수 목록입니다.',
                }))
            });
        }
    }

    return (

        <>
            <div id="join_container">
                <p className="pageTitle">회원가입</p>
                <div id="contents">
                    <div className="joinbox">
                        <p>약관 동의</p>
                        <form>
                            <div className="agree all">
                                <label htmlFor="agree_all">
                                    회원가입 약관을 모두 확인했습니다.
                                </label>
                                <input
                                    type="checkbox"
                                    name="register_agree"
                                    id="agree_all"
                                    value="agree_all"
                                    onChange={(e) => handleAllCheck(e.target.checked)}
                                    checked={isCheckedAll}
                                />
                            </div>
                            <div className="agree use">
                                <label htmlFor="agree_use">
                                    이용약관 동의 <span>&#42;필수</span>
                                </label>
                                <input
                                    type="checkbox"
                                    name="register_agree"
                                    id="agree_use"
                                    value="agree_use"
                                    onChange={() => handleItemCheck('agree_use')}
                                    checked={checkItems.includes('agree_use')}
                                />
                                {errorMessage.agree_use && <p>{errorMessage.agree_use}</p>}
                            </div>
                            <div className="termOfRegister">
                                <p>
                                    제22조(저작권의 귀속 및 이용제한) ① “몰“이
                                    작성한 저작물에 대한 저작권 기타
                                    지적재산권은 ”몰“에 귀속합니다. ② 이용자는
                                    “몰”을 이용함으로써 얻은 정보 중 “몰”에게
                                    지적재산권이 귀속된 정보를 “몰”의 사전 승낙
                                    없이 복제, 송신, 출판, 배포, 방송 기타
                                    방법에 의하여 영리목적으로 이용하거나
                                    제3자에게 이용하게 하여서는 안됩니다. ③
                                    “몰”은 약정에 따라 이용자에게 귀속된
                                    저작권을 사용하는 경우 당해 이용자에게
                                    통보하여야 합니다. 제23조(분쟁해결) ① “몰”은
                                    이용자가 제기하는 정당한 의견이나 불만을
                                    반영하고 그 피해를 보상처리하기 위하여
                                    피해보상처리기구를 설치․운영합니다. ② “몰”은
                                    이용자로부터 제출되는 불만사항 및 의견은
                                    우선적으로 그 사항을 처리합니다. 다만,
                                    신속한 처리가 곤란한 경우에는 이용자에게 그
                                    사유와 처리일정을 즉시 통보해 드립니다. ③
                                    “몰”과 이용자 간에 발생한 전자상거래 분쟁과
                                    관련하여 이용자의 피해구제신청이 있는
                                    경우에는 공정거래위원회 또는 시·도지사가
                                    의뢰하는 분쟁조정기관의 조정에 따를 수
                                    있습니다. 제24조(재판권 및 준거법) ① “몰”과
                                    이용자 간에 발생한 전자상거래 분쟁에 관한
                                    소송은 제소 당시의 이용자의 주소에 의하고,
                                    주소가 없는 경우에는 거소를 관할하는
                                    지방법원의 전속관할로 합니다. 다만, 제소
                                    당시 이용자의 주소 또는 거소가 분명하지
                                    않거나 외국 거주자의 경우에는 민사소송법상의
                                    관할법원에 제기합니다. ② “몰”과 이용자 간에
                                    제기된 전자상거래 소송에는 한국법을
                                    적용합니다.
                                </p>
                            </div>
                            <div className="agree personal">
                                <label htmlFor="agree_personal">
                                    개인정보 수집 및 이용 동의{' '}
                                    <span>&#42;필수</span>
                                </label>
                                <input
                                    type="checkbox"
                                    name="register_agree"
                                    id="agree_personal"
                                    value="agree_personal"
                                    onChange={() => handleItemCheck('agree_personal')}
                                    checked={checkItems.includes('agree_personal')}
                                />
                                {errorMessage.agree_personal && <p>{errorMessage.agree_personal}</p>}
                            </div>
                            <div className="termOfRegister">
                                <p>
                                    개인정보처리방침 [차례] 1. 총칙 2. 개인정보
                                    수집에 대한 동의 3. 개인정보의 수집 및
                                    이용목적 4. 수집하는 개인정보 항목 5.
                                    개인정보 자동수집 장치의 설치, 운영 및 그
                                    거부에 관한 사항 6. 목적 외 사용 및 제3자에
                                    대한 제공 7. 개인정보의 열람 및 정정 8.
                                    개인정보 수집, 이용, 제공에 대한 동의철회 9.
                                    개인정보의 보유 및 이용기간 10. 개인정보의
                                    파기절차 및 방법 11. 아동의 개인정보 보호
                                    12. 개인정보 보호를 위한 기술적 대책 13.
                                    개인정보의 위탁처리 14. 의겸수렴 및 불만처리
                                    15. 부 칙(시행일) 1. 총칙 본 업체 사이트는
                                    회원의 개인정보보호를 소중하게 생각하고,
                                    회원의 개인정보를 보호하기 위하여 항상
                                    최선을 다해 노력하고 있습니다. 1) 회사는
                                    「정보통신망 이용촉진 및 정보보호 등에 관한
                                    법률」을 비롯한 모든 개인정보보호 관련
                                    법률규정을 준수하고 있으며, 관련 법령에
                                    의거한 개인정보처리방침을 정하여 이용자 권익
                                    보호에 최선을 다하고 있습니다. 2) 회사는
                                    「개인정보처리방침」을 제정하여 이를
                                    준수하고 있으며, 이를 인터넷사이트 및 모바일
                                    어플리케이션에 공개하여 이용자가 언제나
                                    용이하게 열람할 수 있도록 하고 있습니다. 3)
                                    회사는 「개인정보처리방침」을 통하여
                                    귀하께서 제공하시는 개인정보가 어떠한 용도와
                                    방식으로 이용되고 있으며 개인정보보호를 위해
                                    어떠한 조치가 취해지고 있는지 알려드립니다.
                                    4) 회사는 「개인정보처리방침」을 홈페이지 첫
                                    화면 하단에 공개함으로써 귀하께서 언제나
                                    용이하게 보실 수 있도록 조치하고 있습니다.
                                    5) 회사는 「개인정보처리방침」을 개정하는
                                    경우 웹사이트 공지사항(또는 개별공지)을
                                    통하여 공지할 것입니다. 2. 개인정보 수집에
                                    대한 동의 귀하께서 본 사이트의
                                    개인정보보호방침 또는 이용약관의 내용에 대해
                                    「동의 한다」버튼 또는 「동의하지
                                    않는다」버튼을 클릭할 수 있는 절차를
                                    마련하여, 「동의 한다」버튼을 클릭하면
                                    개인정보 수집에 대해 동의한 것으로 봅니다.
                                    3. 개인정보의 수집 및 이용목적 본 사이트는
                                    다음과 같은 목적을 위하여 개인정보를
                                    수집하고 있습니다. 서비스 제공을 위한 계약의
                                    성립 : 본인식별 및 본인의사 확인 등 서비스의
                                    이행 : 상품배송 및 대금결제 회원 관리 :
                                    회원제 서비스 이용에 따른 본인확인, 개인
                                    식별, 연령확인, 불만처리 등 민원처리 기타
                                    새로운 서비스, 신상품이나 이벤트 정보 안내
                                    단, 이용자의 기본적 인권 침해의 우려가 있는
                                    민감한 개인정보(인종 및 민족, 사상 및 신조,
                                    출신지 및 본적지, 정치적 성향 및 범죄기록,
                                    건강상태 등)는 수집하지 않습니다. 4.
                                    수집하는 개인정보 항목 본 사이트는 회원가입,
                                    상담, 서비스 신청 등을 위해 아래와 같은
                                    개인정보를 수집하고 있습니다. 1) 개인정보
                                    수집방법 : 홈페이지(회원가입) 2) 수집항목 :
                                    이름 , 생년월일 , 성별 , 로그인ID , 비밀번호
                                    , 전화번호 , 주소 , 휴대전화번호 , 이메일 ,
                                    주민등록번호 , 접속 로그 , 접속 IP 정보 ,
                                    결제기록 5. 개인정보 자동수집 장치의 설치,
                                    운영 및 그 거부에 관한 사항 본 사이트는
                                    귀하에 대한 정보를 저장하고 수시로 찾아내는
                                    '쿠키(cookie)'를 사용합니다. 쿠키는
                                    웹사이트가 귀하의 컴퓨터
                                    브라우저(넷스케이프, 인터넷 익스플로러 등)로
                                    전송하는 소량의 정보입니다. 귀하께서
                                    웹사이트에 접속을 하면 본 사이트의 컴퓨터는
                                    귀하의 브라우저에 있는 쿠키의 내용을 읽고,
                                    귀하의 추가정보를 귀하의 컴퓨터에서 찾아
                                    접속에 따른 성명 등의 추가 입력 없이
                                    서비스를 제공할 수 있습니다. 1) 쿠키는
                                    귀하의 컴퓨터는 식별하지만 귀하를 개인적으로
                                    식별하지는 않습니다. 또한 귀하는 쿠키에 대한
                                    선택권이 있습니다. 웹브라우저의 옵션을
                                    조정함으로써 모든 쿠키를 다 받아들이거나,
                                    쿠키가 설치될 때 통지를 보내도록 하거나,
                                    아니면 모든 쿠키를 거부할 수 있는 선택권을
                                    가질 수 있습니다. 2) 쿠키 등 사용 목적 :
                                    이용자의 접속 빈도나 방문 시간 등을 분석,
                                    이용자의 취향과 관심분야를 파악 및 자취
                                    추적, 각종 이벤트 참여 정도 및 방문 회수
                                    파악 등을 통한 타겟 마케팅 및 개인 맞춤
                                    서비스 제공 3) 쿠키 설정 거부 방법 : 쿠키
                                    설정을 거부하는 방법으로는 귀하가 사용하는
                                    웹 브라우저의 옵션을 선택함으로써 모든
                                    쿠키를 허용하거나 쿠키를 저장할 때마다
                                    확인을 거치거나, 모든 쿠키의 저장을 거부할
                                    수 있습니다. 설정방법 예시 : 인터넷
                                    익스플로어의 경우 → 웹 브라우저 상단의 도구
                                    &gt; 인터넷 옵션 &gt; 개인정보 단, 귀하께서
                                    쿠키 설치를 거부하였을 경우 서비스 제공에
                                    어려움이 있을 수 있습니다. 6 목적 외 사용 및
                                    제3자에 대한 제공 본 사이트는 귀하의
                                    개인정보를 "개인정보의 수집목적 및
                                    이용목적"에서 고지한 범위 내에서 사용하며,
                                    동 범위를 초과하여 이용하거나 타인 또는 타
                                    기업·기관에 제공하지 않습니다. 그러나 보다
                                    나은 서비스 제공을 위하여 귀하의 개인정보를
                                    업체 자회사 또는 제휴사에게 제공하거나, 업체
                                    자회사 또는 제휴사와 공유할 수 있습니다.
                                    개인정보를 제공하거나 공유할 경우에는 사전에
                                    귀하께 업체 자회사 그리고 제휴사가 누구인지,
                                    제공 또는 공유되는 개인정보항목이 무엇인지,
                                    왜 그러한 개인정보가 제공되거나 공유되어야
                                    하는지, 그리고 언제까지 어떻게
                                    보호·관리되는지에 대해 개별적으로 전자우편
                                    및 서면을 통해 고지하여 동의를 구하는 절차를
                                    거치게 되며, 귀하께서 동의하지 않는 경우에는
                                    업체 자회사 그리고 제휴사에게 제공하거나
                                    공유하지 않습니다. 또한 이용자의 개인정보를
                                    원칙적으로 외부에 제공하지 않으나, 아래의
                                    경우에는 예외로 합니다. 1) 이용자들이 사전에
                                    동의한 경우 2) 법령의 규정에 의거하거나,
                                    수사 목적으로 법령에 정해진 절차와 방법에
                                    따라 수사기관의 요구가 있는 경우 7.
                                    개인정보의 열람 및 정정 귀하는 언제든지
                                    등록되어 있는 귀하의 개인정보를 열람하거나
                                    정정하실 수 있습니다. 개인정보 열람 및
                                    정정을 하고자 할 경우에는 "회원정보수정"을
                                    클릭하여 직접 열람 또는 정정하거나, 개인정보
                                    최고관리책임자에게 E-mail로 연락하시면
                                    조치하겠습니다. 귀하가 개인정보의 오류에
                                    대한 정정을 요청한 경우, 정정을 완료하기
                                    전까지 당해 개인정보를 이용하지 않습니다. 8.
                                    개인정보 수집, 이용, 제공에 대한 동의철회
                                    회원가입 등을 통해 개인정보의 수집, 이용,
                                    제공에 대해 귀하께서 동의하신 내용을 귀하는
                                    언제든지 철회하실 수 있습니다. 동의철회는
                                    "마이페이지"의 "회원탈퇴(동의철회)"를
                                    클릭하거나 개인정보관리책임자에게
                                    E-mail등으로 연락하시면 즉시 개인정보의 삭제
                                    등 필요한 조치를 하겠습니다. 본 사이트는
                                    개인정보의 수집에 대한 회원탈퇴(동의철회)를
                                    개인정보 수집시와 동등한 방법 및 절차로
                                    행사할 수 있도록 필요한 조치를 하겠습니다.
                                    9. 개인정보의 보유 및 이용기간 원칙적으로,
                                    개인정보 수집 및 이용목적이 달성된 후에는
                                    해당 정보를 지체 없이 파기합니다. 단, 다음의
                                    정보에 대해서는 아래의 이유로 명시한 기간
                                    동안 보존합니다. 1) 보존 항목 :
                                    회원가입정보(로그인ID, 이름, 별명) 2) 보존
                                    근거 : 회원 탈퇴 시 다른 회원이 기존
                                    회원아이디로 재가입하여 활동하지 못하도록
                                    하기 위함 3) 보존 기간 : 영구 그리고 상법 등
                                    관계법령의 규정에 의하여 보존할 필요가 있는
                                    경우 회사는 아래와 같이 관계법령에서 정한
                                    일정한 기간 동안 거래 및 회원정보를
                                    보관합니다. ⚪보존 항목 : 계약 또는 청약철회
                                    기록, 대금 결제 및 재화공급 기록, 불만 또는
                                    분쟁처리 기록 ⚪보존 근거 :
                                    전자상거래등에서의 소비자보호에 관한 법률
                                    제6조 거래기록의 보존 ⚪보존 기간 : 계약
                                    또는 청약철회 기록(5년), 대금 결제 및
                                    재화공급 기록(5년), 불만 또는 분쟁처리
                                    기록(3년), 위 보유기간에도 불구하고 계속
                                    보유하여야 할 필요가 있을 경우에는 귀하의
                                    동의를 받겠습니다. ⚪회원이 1년간 서비스
                                    이용기록이 없는 경우[정보통신망 이용촉진 및
                                    정보보호 등에 관한 법률] 제 29조 '개인정보
                                    유효 기간제'에 따라 회원에게 사전 통지하고
                                    별도로 분리하여 저장합니다. 4) 개인정보의
                                    국외 보관에 대한 내용 회사는 이용자로부터
                                    취득 또는 생성한 개인정보를 미리내가
                                    보유하고 있는 데이터베이스 (물리적보관소:
                                    한국)에 저장합니다. 미리내는 해당 서버의
                                    물리적인 관리만을 행하고, 원칙적으로
                                    회원님의 개인정보에 접근하지 않습니다.
                                    ⚪이전항목: 서비스 이용기록 또는 수집된
                                    개인정보 ⚪이전국가: 한국 ⚪이전일시 및
                                    방법: 전산 서버 수탁계약 이후 서비스 이용
                                    시점에 네트워크를 통한 전송 ⚪개인정보 보유
                                    및 이용기간: 회원탈퇴시 혹은 위탁계약
                                    종료시까지 10. 개인정보의 파기절차 및 방법
                                    본 사이트는 원칙적으로 개인정보 수집 및
                                    이용목적이 달성된 후에는 해당 정보를 지체
                                    없이 파기합니다. 파기절차 및 방법은 다음과
                                    같습니다. 파기절차 : 귀하가 회원가입 등을
                                    위해 입력하신 정보는 목적이 달성된 후 별도의
                                    DB로 옮겨져(종이의 경우 별도의 서류함) 내부
                                    방침 및 기타 관련 법령에 의한 정보보호
                                    사유에 따라(보유 및 이용기간 참조) 일정 기간
                                    저장된 후 파기되어집니다. 별도 DB로 옮겨진
                                    개인정보는 법률에 의한 경우가 아니고서는
                                    보유되어지는 이외의 다른 목적으로 이용되지
                                    않습니다. 파기방법 : 전자적 파일형태로
                                    저장된 개인정보는 기록을 재생할 수 없는
                                    기술적 방법을 사용하여 삭제합니다. 11.
                                    아동의 개인정보 보호 본 사이트는 만14세 미만
                                    아동의 개인정보를 수집하는 경우 법정대리인의
                                    동의를 받습니다. 만14세 미만 아동의
                                    법정대리인은 아동의 개인정보의 열람, 정정,
                                    동의철회를 요청할 수 있으며, 이러한 요청이
                                    있을 경우 본 사이트는 지체 없이 필요한
                                    조치를 취합니다. 12. 개인정보 보호를 위한
                                    기술적 대책 본 사이트는 귀하의 개인정보를
                                    취급함에 있어 개인정보가 분실, 도난, 누출,
                                    변조 또는 훼손되지 않도록 안전성 확보를
                                    위하여 다음과 같은 기술적 대책을 강구하고
                                    있습니다. 귀하의 개인정보는 비밀번호에 의해
                                    보호되며, 파일 및 전송 데이터를 암호화하거나
                                    파일 잠금기능(Lock)을 사용하여 중요한
                                    데이터는 별도의 보안기능을 통해 보호되고
                                    있습니다. 본 사이트는 백신프로그램을
                                    이용하여 컴퓨터바이러스에 의한 피해를
                                    방지하기 위한 조치를 취하고 있습니다.
                                    백신프로그램은 주기적으로 업데이트되며
                                    갑작스런 바이러스가 출현할 경우 백신이
                                    나오는 즉시 이를 제공함으로써 개인정보가
                                    침해되는 것을 방지하고 있습니다. 해킹 등에
                                    의해 귀하의 개인정보가 유출되는 것을
                                    방지하기 위해, 외부로부터의 침입을 차단하는
                                    장치를 이용하고 있습니다. 13. 개인정보의
                                    위탁처리 본 사이트는 서비스 향상을 위해서
                                    귀하의 개인정보를 외부에 위탁하여 처리할 수
                                    있습니다. 개인정보의 처리를 위탁하는
                                    경우에는 미리 그 사실을 귀하에게
                                    고지하겠습니다. 개인정보의 처리를 위탁하는
                                    경우에는 위탁계약 등을 통하여 서비스제공자의
                                    개인정보보호 관련 지시 엄수, 개인정보에 관한
                                    비밀유지, 제3자 제공의 금지 및 사고시의
                                    책임부담 등을 명확히 규정하고 당해
                                    계약내용을 서면 또는 전자적으로
                                    보관하겠습니다. 14. 의견수렴 및 불만처리 본
                                    사이트는 개인정보보호와 관련하여 귀하가
                                    의견과 불만을 제기할 수 있는 창구를 개설하고
                                    있습니다. 개인정보와 관련한 불만이 있으신
                                    분은 본 사이트의 개인정보 최고
                                    관리책임자에게 의견을 주시면 접수 즉시
                                    조치하여 처리결과를 통보해 드립니다. 1)
                                    개인정보 최고 관리책임자는 회사의 대표가
                                    직접 맡아서 개인정보에 대한 강조를 하고
                                    있습니다. 개인정보를 보호하고 유출을
                                    방지하는 책임자로서 이용자가 안심하고 회사가
                                    제공하는 서비스를 이용할 수 있도록
                                    도와드리며, 개인정보를 보호하는데 있어
                                    이용자에게 고지한 사항들에 반하여 사고가
                                    발생할 시에는 이에 관한 책임을 집니다. 2)
                                    기술적인 보완조치를 취하였음에도 불구하고
                                    악의적인 해킹 등 기본적인 네트워크상의
                                    위험성에 의해 발생하는 예기치 못한 사고로
                                    인한 정보의 훼손 및 멸실, 이용자가 작성한
                                    게시물에 의한 각종 분쟁 등에 관해서는, 본
                                    사이트 회사는 책임이 없습니다. 3) 회사는
                                    정보통신망 이용촉진 및 정보보호 등에 관한
                                    법률에서 규정한 관리책임자를 다음과 같이
                                    지정합니다. 개인정보 최고 관리책임자 성명 :
                                    전화번호 : 이메일 : 또는 개인정보침해에 대한
                                    신고나 상담이 필요하신 경우에는 아래 기관에
                                    문의하시기 바랍니다. 개인분쟁조정위원회
                                    (www.1336.or.kr / 1336)
                                    정보보호마크인증위원회 (www.eprivacy.or.kr /
                                    02-580-0533~4) 대검찰청 인터넷범죄수사센터
                                    (icic.sppo.go.kr / 02-3480-3600) 경찰청
                                    사이버테러대응센터 (www.ctrc.go.kr /
                                    02-392-0330) 15. 부 칙(시행일) 현
                                    개인정보처리방침은 2017년 9월 22일에
                                    제정되었으며, 정부 및 회사의 정책 또는
                                    보안기술의 변경에 따라 내용의 추가, 삭제 및
                                    수정이 있을 경우에는 개정 최소 7일 전부터
                                    ‘공지사항’란을 통해 고지하며, 본 정책은 시행
                                    일자에 시행됩니다. 1) 공고일자 : 2018년 05월
                                    01일 2) 시행일자 : 2018년 05월 01일
                                </p>
                            </div>
                            <div className="agree age">
                                <label htmlFor="agree_age">
                                    만 19세 이상입니다. <span>&#42;필수</span>
                                </label>
                                <input
                                    type="checkbox"
                                    name="register_agree"
                                    id="agree_age"
                                    value="agree_age"
                                    onChange={() => handleItemCheck('agree_age')}
                                    checked={checkItems.includes('agree_age')}
                                />
                                {errorMessage.agree_age && <p>{errorMessage.agree_age}</p>}

                            </div>
                            <div className="agree_submit">
                                <button onClick={(e)=>{
                                    e.preventDefault();
                                    navigate(-1);
                                }}>취소</button>
                                <button onClick={nextPage}>확인</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Join;
